'use strict';

console.log('hello world');

//selecting elements//

const dayDiv=document.querySelector('.light');
const darkDiv=document.querySelector('.dark');
let appStat=false; //true equals day, false equals night
/* const buttonTest=document.querySelector('.activate-function'); */
const ball= document.querySelector('.ball');
//images//
const dayImg=document.querySelector('.light-img');
const nightImg=document.querySelector('.night-img');

//lights//
const innerLight=document.querySelector('.inner-light');
//switch container//
const container=document.querySelector('.toggle-container');
//shuting star//
const shuttingStart=document.querySelector('.shoot-2');


const toggleFunction=function(){
    if(appStat===false){
        dayDiv.style.transform='translateX(-100%)';
        darkDiv.style.transform='translateX(0%)';
        //animation/
/* 
        darkDiv.classList.add('dark-animation');
        dayDiv.classList.remove('day-animation'); */

        nightImg.classList.add('resolve-night');
        dayImg.classList.remove('resolve-day');

        //turing on off light//
        innerLight.classList.toggle('light-on');
        //darken the container for the inner light//
        container.classList.toggle('container-darken');

        console.log('clicked night')

    }else if(appStat===true){
        dayDiv.style.transform='translateX(0%)';
        darkDiv.style.transform='translateX(100%)';
/* 
        dayDiv.classList.add('day-animation');
        darkDiv.classList.remove('dark-animation'); */

        dayImg.classList.add('resolve-day');
        nightImg.classList.remove('resolve-night')
        //controlling light//
        innerLight.classList.toggle('light-on');
         //darken the container for the inner light//
         container.classList.toggle('container-darken');

        console.log('clicked-❤️day')

    }
}
const toggleAppStatus=function(){

    if(appStat===false){
        appStat=true;
    }else if(appStat==true){
        appStat=false;
    }
}

ball.addEventListener('click',function(){
    if(ball.classList.contains('ball-to-right')){
        ball.classList.remove('ball-to-right');
        ball.classList.add('ball-to-left');
        toggleAppStatus();
        toggleFunction();

    }else if(ball.classList.contains('ball-to-left')){
        ball.classList.remove('ball-to-left');
        ball.classList.add('ball-to-right');
        toggleAppStatus();
        toggleFunction();
    }
})

