///EFFECTS///

const shootingStarEffect=function(){
    shuttingStart.classList.add('shutting-star-animation-class');

    setTimeout(function(){
   shuttingStart.classList.remove('shutting-star-animation-class');
    },2500);
}

setInterval(function(){
    shootingStarEffect();
}, 20000);



